  padding: var(--grid-item-content-padding, 0.68267rem 0.34133rem);
  background-color: #fff;
  background-color: var(--grid-item-content-background-color, #fff);
}
.van-grid-item__content::after {
  z-index: 1;
  border-width: 0 0.04267rem 0.04267rem 0;
  border-width: 0 var(--border-width-base, 0.04267rem) var(--border-width-base, 0.04267rem) 0;
}
